ul{
    list-style-type: none;
    padding: 0;
    /* padding-left: 10%; */
}

li{
    padding: 8%;
    padding-left: 15%;
    text-align: left;
    color: #5C5C5C;
    font-weight: bold;
}

a{
    margin-left: 15%;
    color: #5C5C5C;
}

.header{
    width: 100%;
}

.active {
    color: white;
    background-color: #6F488E;
    width: 100%;
}

.active a{
    color: white;
}

a:hover{
    color: black;
}

.avatarContainer{
    padding-top: 15%;
    padding-bottom: 20%;
}

div.sidebar-wrapper div div{       
    width: 100%;
}

.row{
    /* height: 100%; */
}
.sb-avatar__text div span,  .sb-avatar.sb-avatar--icon{
    box-sizing: content-box;
}
.col-lg-2{
    padding: 0;
}
.sb-avatar.sb-avatar--text{
    margin-right: 5%;
}

.non-exact-match{
    color: #5C5C5C;
}

.disabled-menu{
    display: inline;
    margin-left: 15%;
    opacity: 50%;
}
.dashboard-sign-up-button{
    margin-left: 15%;
    color: #5C5C5C;
    display: inline;
}
.dashboard-sign-up-button:hover{
    color: black;
    cursor: pointer;
}
