.form-control{
    border: 1px solid #AD86FF;
}
.form-group, .form-row {
    width: 100%;
    margin: 20px auto 0 auto;
}
button.btn.btn-primary{
    background-color: #6F488E;
    border-color: #6F488E;
    box-shadow: 1px 3px 5px #888888;
    border-radius: 0;
    width: 40%;
    margin: 10px auto auto auto;
}
input.form-control{
    background-color: #F0F0F0;
    margin-top: 20px;
    border-radius: 15px;
}
.horizontalLine {
    width: 100%; 
    text-align: center; 
    border-bottom: 1px solid #6F488E; 
    line-height: 0.1em;
    margin: 20px 0 20px;
 }
 
 .horizontalLine span { 
     background:#fff; 
     padding:0 10px; 
     color: #6F488E;
 }
div.modal-header{
     background-color: #AD86FF;
 }
 div.modal-footer{
    border:0px;
}
p{
    width: 100%;
    margin-bottom: 0;
    margin-top: 15px;
}
#userManagementSignInButtons, #userManagementSignUpButtons{
    width: 100%;
    text-align: center;
}
#modalContinueWithGoogleButton{
    padding-right: 0px !important;
    padding-left: 0px !important;
}
button.close{
    background-color:transparent !important;
    box-shadow: none !important;
    margin: 0 !important;
}
button.close span{
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1;
    color: #000;
    text-shadow: 0 1px 0 #fff;
    float: right;
}
