.header{
    padding: 3%;
}

.inputField{
    border: 2px solid #AD86FF;
    border-radius: 25px;
    background-color: #F0F0F0;
    margin: 3%;
    width: 75%;
    text-align: center ;
}
.inputField:focus{
    outline-width: 0;
}
.correct{
    color: green;
    box-shadow: 0 0 10px 5px;
}
.wrong{
    color: red;
    box-shadow: 0 0 10px 5px;
}