.app-header {
    top: 0;
    background-color: var(--main-purple);
    height: 100px;
    width: 100%;
    display: block;
    text-align: center;
    justify-content: center;
    flex-direction: column;
    box-shadow: 2px 3px 9px 1px rgba(0,0,0,0.3);
}

.header-logo-wrapper{
    height: 90px;
    width: 90px;
    border: 2px solid white;
    border-radius: 50px;
    display: inline-block;
    float: left;
    margin-top: 10px;
}

.header-title{
    display: inline-block;
    font-size: 14pt;
    color: white;
    font-weight: 800;
    vertical-align: top;
    padding-top: 40px;
    padding-left: 10px;
    float: left;
}

.header-logo{
    width: 100%;
    float: left;
    padding: 15px 5px;
}

.left-header-icon{
    display: inline-block;
    width: calc(50% - 100px);
    padding-left: 100px;
    float: left;
    height: 100%;
}

.right-header-buttons{
    display: flex;
    flex-direction: row-reverse;
    width: calc(50% - 100px);
    padding-right: 100px;
    float: right;
    padding-top: 50px;
    height: calc(100% - 65px);
}

.header-button{
    display: inline-block;
    float: right;
    font-weight: 700;
    font-size: 10pt;
    max-width: 150px;
    padding-left: 50px;
}

.header-material-button{
    transform: translate(0px, -25px);

}

button{
    font-weight: 600 !important;
    height: 43px !important;
    width: inherit !important;
}

.navbar.navbar-expand-lg.navbar-dark.bg-dark{
    background-color: #AD86FF !important;
}
.navbar-brand
{
    text-align: center;
    margin: auto;
    padding:0;
}
.navbar-dark .navbar-nav .nav-link {
    color: #5C5C5C;
    width: 100%;
    padding-right: 20%;
}
div.navbar-nav{
    padding-right: 10%;
}
