.edit-profile-component{
    margin-top: 4%;
    padding-left: 3%;
    text-align: left;
    height: 90%;
}

.main-section{
    width: 450px;
    display: grid;
    padding-top: 10px;
}

.section-title{
    font-size: 16pt;
    font-weight: 600;
    padding-top: 20px;
}

.form-group{
    margin-top: 0 !important;
}

.form-row{
    display: inline-table;
}

.form-cell{
    display: table-cell;
    vertical-align: middle;
    font-size: 10.5pt;
    font-weight: 500;
}

.save-button-container{
    width: 100px;
    padding-top: 30px;
    float: left;
}

.settings-form {
    padding-top: 20px;
}

.settings-form .form-group{
    display: grid;
}

.settings-form .form-cell{
    padding-top: 10px;
}

.MuiButtonBase-root{
    width: 50px !important;
    display: inline-block;
}

.edit-profile-component input{
    width: 300px !important;
    float: right;
    margin-top: 0 !important;
    height: 30px !important;
    font-size: 10pt !important;
}
.header-dashboard{
    text-align: left;
    padding: 3% 3% 0% 3%;
}
.description{
    text-align: left;
    padding-left: 3%;
}