.app-homepage{
    width: 100%;
}

.homepage-section-wrapper{
    display: block;
    width: 100%;
    min-height: 100px;
    height: auto;
}

.first-section{
    background-image: url("../../assets/homepage-lady.svg");
    background-size: 100% auto;
    background-repeat: no-repeat;
    margin-bottom: 50px;
}

.homepage-section-divider{
    margin: 20px 0;
    border-top: 1px solid var(--purple-dark);
    height: 1px;
    display: block;
    width: 100%;
}

.homepage-section-title{
    display: inline-block;
    transform: translate(0, -30px);
    padding: 10px;
    background-color: white;
    color: var(--purple-dark);
    font-size: 20pt;
    font-weight: 700;
}

.homepage-get-started-container{
    position: absolute;
    width: 430px;
    margin: 0 100px;
    padding: 30px 20px 20px 20px;
    background-color: var(--purple-paler);
}

.homepage-get-started-title{
    display: block;
    font-size: 17pt;
    font-weight: 700;
    color: var(--purple-dark);
    text-align: justify;
    padding-bottom: 20px;
}

.homepage-subtitle{
    display: inline-block;
    font-size: 12pt;
    font-weight: 500;
    text-align: justify;
    line-height: 25px;
    padding-bottom: 10px;
    max-width: 430px;
}

.homepage-section-buttons-container{
    max-width: 430px;
    width: 100%;
}

.homepage-section-button{
    display: inline-block;
    width: 45%;
}

.homepage-section{
    display: table;
    width: 100%;
    padding: 50px 0;
}

.homepage-last-section{
    display: table;
    width: 100%;
    padding: 20px 0 50px 0;
}

.homepage-icon-container{
    width: 300px;
}

.vertical-align{
    padding-top: 50px;
}

.homepage-links-wrapper{
    display: table;
    width: 100%;
}

.homepage-links-section{
    display: inline-grid;
    width: calc(33% - 100px);
    text-align: justify;
    padding-left: 100px;
}

@media only screen and (max-width: 900px) {
    .first-section{
        height: 400px;
    }

    .homepage-get-started-container{
        top: 150px;
    }
}

@media only screen and (min-width:901px) and (max-width:1399px) {
    .first-section{
        height: 600px;
    }

    .homepage-get-started-container{
        top: 150px;
    }
}

@media only screen and (min-width: 1400px) {
    .first-section{
        height: 700px;
    }

    .homepage-get-started-container{
        top: 300px;
    }
}


