.camera-quiz-wrapper{
    display: initial;
    visibility: hidden;
}

.box-wrapper{
    width: 100%;
    height: 5px;
    text-align: center;
}

.box{
    height: 250px;
    width: 250px;
    display: inline-block;
    transform: translate(190px, -480px);
    border: green 8px solid;
}

.counter-wrapper{
    width: 100%;
    transform: translate(0px, -400px);
    height: 1px;
}

.counter{
    color: white;
    font-size: 100pt;
    font-weight: 600;
    display: inline-block;
}

.skip-question-wrapper{
    width: 100%;
    height: 400px;
    background-color: lightgrey;
}

.skip-button-wrapper{
    width: 100px;
    position: relative;
    top: 50%;
    left: calc(50% - 50px);
}

.skip-text-wrapper{
    position: relative;
    top: 20%;
    text-align: center
}


.answer-feedback{
    font-size: 16pt;
}

.correct-answer{
    color: green;
}
.wrong-answer{
    color: red;
}

.button-wrapper button:disabled{
    opacity: 0.5;
}
