@import "./styles/colors.css";

.App {
  text-align: center;
  height: 100%;
}

#root{
  position: absolute;
  height: 100%;
  width: 100%;
}

a{
  color: var(--links-blue);
  text-decoration: none !important;
}

/*app buttons*/
button{
  color: white !important;
  font-size: 10pt !important;
  font-weight: 600 !important;
  padding: 10px 20px !important;
  border-radius: 0 !important;
  margin: 10px 0px !important;
  background-color: var(--purple-dark) !important;
  display: inline !important;
  box-shadow: 2px 3px 6px 1px rgba(0,0,0,0.5) !important;
  text-transform: capitalize !important;
  width: 100% !important;
}

.MuiButtonBase-root{
  width: 100% !important;
}

.white-txt{
  color: white !important;
}

.section-half-cell{
  display: inline-block;
  width: 50%;
}

.right{
  float: right;
  position: relative;
}

.left{
  float: left;
  position: relative;
}

.spacing-div-20{
  display: block;
  height: 40px;
}

.invalid-input{
  color: red;
}

.success-message{
  color: green;
}

