input.btn.btn-primary{
    display: block;
    width: 50%;
    height: 55px;
    /* margin: 2%; */
    border-radius: 20px;
    border-color: #5C5C5C;
    background-color: rgba(173, 134, 255, 0.1);
    color: black;
    font-weight: bold;
    text-align: left;
    box-shadow: inset 2px 3px rgba(0, 0, 0, 0.1);
}
.module-buttons-wrapper{
    margin-top: 4%;
}
div.App div{
    position: relative !important;
}
.module-hr{
    width: 100%;
    position: absolute;
    z-index: 2;
    margin-top: 10%;
}
.row{
    /* height: 100%; */
}
.checkmark-logo{
    height: 20px;
    margin-top: 18px;
}
.module-row{
    display: flex;
    width: 50%;
    height: 55px;
    /* margin: 2%; */
    border-radius: 20px;
    border-color: #5C5C5C;
    background-color: rgba(173, 134, 255, 0.1);
    color: black;
    font-weight: bold;
    box-shadow: inset 2px 3px rgba(0, 0, 0, 0.1);
    margin-left: 2%;
}

.module-row:hover{
    box-shadow: inset 2px 3px rgba(0, 0, 0, 0.2);
    background-color: #6F488E;
    color: white;
    font-weight: 800;
}
.module-title{

    text-align: left;
}
